import urlParam from './decode-url';

// config
const config = {
    'show_fabrication': false,
    'redraw_on_drag': true,
    'highlight_pin1': false,
    'extra_fields': [],
    'dark_mode': true,
    'bom_view': 'left-right',
    'board_rotation': 0.0,
    'checkboxes': 'Sourced,Placed',
    'show_silkscreen': true,
    'show_pads': true,
    'layer_view': 'FB'
}

// pcbdata
import aod_deflect_data from '../data/aod-deflect.json';
import car_data from '../data/car.json';
import dvd_control_data from '../data/dvd-control.json';
import peltier_cooler_data from '../data/peltier_cooler.json';
import phase_lock_data from '../data/phase_lock.json';
import pulse_oximeter_data from '../data/pulse_oximeter.json';
import watch_timer_data from '../data/watch-timer.json';

// Front and back images
import aod_deflect_front from '../data/aod-deflect-front.png';
import aod_deflect_back from '../data/aod-deflect-back.png';
import car_front from '../data/car-front.png';
import car_back from '../data/car-back.png';
import peltier_cooler_front from '../data/peltier_cooler-front.png';
import peltier_cooler_back from '../data/peltier_cooler-back.png';
import pulse_oximeter_front from '../data/pulse_oximeter-front.png';
import pulse_oximeter_back from '../data/pulse_oximeter-back.png';
import dvd_control_front from '../data/dvd-control-front.png';
import dvd_control_back from '../data/dvd-control-back.png';
import phase_lock_front from '../data/PhaseLock-front.png';
import phase_lock_back from '../data/PhaseLock-back.png';
import watch_timer_front from '../data/watch-timer-front.png';
import watch_timer_back from '../data/watch-timer-back.png';

type bundle_t = {
    pcbdata: object; silkscreen_front: string; silkscreen_back: string;
};

function getBOM(tag: string|null): bundle_t {
    switch (tag) {
        case 'car':
            return {pcbdata: car_data, silkscreen_front: car_front, silkscreen_back: car_back};
        case 'dvd-control':
            return {
                pcbdata: dvd_control_data,
                silkscreen_front: dvd_control_front,
                silkscreen_back: dvd_control_back
            };
        case 'phase_lock':
            return {
                pcbdata: phase_lock_data,
                silkscreen_front: phase_lock_front,
                silkscreen_back: phase_lock_back
            };
        case 'pulse_oximeter':
            return {
                pcbdata: pulse_oximeter_data,
                silkscreen_front: pulse_oximeter_front,
                silkscreen_back: pulse_oximeter_back
            };
        case 'aod-deflect':
            return {
                pcbdata: aod_deflect_data,
                silkscreen_front: aod_deflect_front,
                silkscreen_back: aod_deflect_back
            };
        case 'watch-timer':
            return {
                pcbdata: watch_timer_data,
                silkscreen_front: watch_timer_front,
                silkscreen_back: watch_timer_back
            };
        case 'peltier_cooler':
        default:
            return {
                pcbdata: peltier_cooler_data,
                silkscreen_front: peltier_cooler_front,
                silkscreen_back: peltier_cooler_back
            };
    }
}

const {pcbdata, silkscreen_front, silkscreen_back} = getBOM(urlParam('pcbdata'));

export {config, pcbdata, silkscreen_front, silkscreen_back};
