<template>
  <div
    ref="el"
    :style="drag_style"
    class="pcb-image"
    @click.right.prevent="transform = default_zoom"
  >
    <svg
      :viewBox="`0 0 ${width} ${height}`"
      xmlns="http://www.w3.org/2000/svg"
      :draggable="false"
    >
      <svg:style>
          <![CDATA[
        .footprint-icon {
          fill: #f004;
          stroke: #f00;
          stroke-width: 1;
          vector-effect: non-scaling-stroke;
        }
          ]]>
      </svg:style>
      <!-- Background image -->
      <image
        :href="url"
        x="0"
        y="0"
        :width="width"
        :height="height"
        preserveAspectRatio="none"
      />

      <!-- Highlighted footprints -->
      <g>
        <rect
          v-for="{ x, y } in highlighted_footprints"
          :key="(y * width + x).toFixed(3)"
          :x="x - bbox_width / 2"
          :y="y - bbox_width / 2"
          :width="bbox_width"
          :height="bbox_width"
          class="footprint-icon"
        />
      </g>
    </svg>
  </div>
</template>

<style>
.pcb-image {
  width: 100%;
  height: 100%;
  display: block;
  cursor: zoom-in;
}
</style>

<script setup lang="ts">
import {computed, onMounted, ref, useTemplateRef} from 'vue';
import {useDragZoom} from 'vue3-drag-zoom';

import {pcbdata} from '../config';
import {selected_component} from '../data-models/footprints';
import {silkscreen_height as height, silkscreen_width as width,} from '../data-models/pcb-images';

const {url, is_front_side} = defineProps<{url: string; is_front_side: boolean;}>();

type Position = {
    x: number; y: number;
};
const bbox_width = 60;

type Transform = {
    x: number; y: number; scale: number;
};

const default_zoom: Transform = {
    x: 0,
    y: 0,
    scale: 0.71,
};

const transform = ref<Transform>(default_zoom);

const el = useTemplateRef('el');
const {style} = useDragZoom(el, transform, {
    zoomRange: {min: 0.1, max: 10.0, step: 0.2},
    onZoom: (new_transform: Transform) => {
        transform.value = new_transform;
    },
});

const drag_style = computed(() => {
    const new_style = style.value;
    new_style.position = 'relative';
    return new_style;
});

const highlighted_footprints = computed<Position[]>(() => {
    const group_id = selected_component.value;
    if (group_id === null) {
        return [];
    }

    const refdes: string[] = pcbdata.bom.both[group_id][3].map((e) => e[0]);

    const positions: Position[] = [];
    for (const id of refdes) {
        const m = pcbdata.modules.find((e) => {
            return e.layer === (is_front_side ? 'F' : 'B') && e.ref === id;
        });
        if (m === undefined) {
            continue;
        }

        positions.push({x: m.center[0], y: m.center[1]});
    }
    return positions;
});

onMounted(() => {
    // Trigger the zoom event.
    transform.value.scale = 0.7;
});
</script>
