<template>
  <table>
    <thead>
      <tr>
        <th class="Checkbox">Sourced</th>
        <th class="Checkbox">Placed</th>
        <th class="RefDes">RefDes</th>
        <th class="Value">Value</th>
        <th class="Footprint">Footprint</th>
        <th class="Quantity">Quantity</th>
      </tr>
    </thead>
    <tbody>
      <tr
        v-for="[index, item] of components.entries()"
        @click="
          clicked = ~clicked;
          select(index);
        "
        @mouseenter="
          if (!clicked) {
            select(index);
          }
        "
        :class="{
          highlighted: selected_component === index,
        }"
        :key="index"
      >
        <td class="Checkbox"><input type="checkbox" /></td>
        <td class="Checkbox"><input type="checkbox" /></td>
        <td class="RefDes">{{ item.refdes.join(", ") }}</td>
        <td class="Value">{{ item.value }}</td>
        <td class="Footprint">{{ item.footprint }}</td>
        <td class="Quantity">{{ item.qty }}</td>
      </tr>
    </tbody>
  </table>
</template>

<style>
table {
  border-collapse: collapse;
  font-family: Consolas, "DejaVu Sans Mono", Monaco, monospace;
  font-size: 10pt;
  table-layout: fixed;
  width: 100%;
  margin-top: 1px;
}

.Checkbox {
  width: 30px;
}

.Description {
  width: 10%;
}

.Part {
  width: 10%;
}

.Value {
  width: 15%;
}

.Quantity {
  width: 65px;
}

tbody tr:nth-child(even) {
  background-color: #313b40;
}

tbody tr.highlighted:nth-child(n),
tbody tr:hover {
  background-color: #226022;
}

thead th {
  background-color: #3b4749;
  background-clip: padding-box;
}

th,
td {
  border: 1px solid #777;
  padding: 5px;
  word-wrap: break-word;
  text-align: center;
  position: relative;
}
</style>

<script setup lang="ts">
import {components} from '../data-models/components';
import {selected_component} from '../data-models/footprints';

const clicked = false;

function select(component_id: number) {
    selected_component.value = component_id;
}
</script>
