import {createApp} from 'vue';

import TheMainApp from './components/the-main-app.vue';
import {pcbdata} from './config';
import {components} from './data-models/components';
import {silkscreen_height, silkscreen_width} from './data-models/pcb-images';

components.value = pcbdata.bom.both.map(item => {
    return {
        qty: item[0], value: item[1], footprint: item[2], refdes: item[3].map((e) => e[0]),
    }
});

silkscreen_width.value = pcbdata.edges_bbox.maxx;
silkscreen_height.value = pcbdata.edges_bbox.maxy;

createApp(TheMainApp).mount('#root');